import React, { useState, useEffect } from 'react';

function CountdownTimer() {
    const [days, setDays] = useState(0);
    const [hours, setHours] = useState(0);
    const [minutes, setMinutes] = useState(0);
    const [seconds, setSeconds] = useState(0);
  
    useEffect(() => {
      const targetDate = new Date(2025, 1, 1, 13, 0, 0).getTime(); // Target date in milliseconds
  
      const intervalId = setInterval(() => {
        const now = new Date().getTime(); // Current time in milliseconds
        const diffMs = targetDate - now;
  
        if (diffMs <= 0) {
          clearInterval(intervalId);
          // Handle countdown end, e.g., display a message
          return;
        }
  
        const daysLeft = Math.floor(diffMs / 86400000);
        const hoursLeft = Math.floor((diffMs % 86400000) / 3600000);
        const minutesLeft = Math.floor((diffMs % 3600000) / 60000);
        const secondsLeft = Math.floor((diffMs % 60000) / 1000);
  
        setDays(daysLeft);
        setHours(hoursLeft);
        setMinutes(minutesLeft);
        setSeconds(secondsLeft);
      }, 1000);
  
      return () => clearInterval(intervalId);
    }, []);

  return (
    <section className="countdown-wrapper">
      <section id="countdown-container" className="countdown-container">
        <article id="js-countdown" className="countdown">
          <section id="js-days" className="number">{pad(days)}</section>
          <section id="js-separator" className="separator">:</section>
          <section id="js-hours" className="number">{pad(hours)}</section>
          <section id="js-separator" className="separator">:</section>
          <section id="js-minutes" className="number">{pad(minutes)}</section>
          <section id="js-separator" className="separator">:</section>
          <section id="js-seconds" className="number">{pad(seconds)}</section>
        </article>
      </section>
    </section>
  );
}

function pad(number) {
  return ("0" + number).slice(-2);
}

export default CountdownTimer;