import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Container from "react-bootstrap/Container";
import React, { useRef, useState } from "react";

const ContactForm = () => {
    const [formData, setFormData] = useState({
        Name: '',
        Email: '',
        Message: '',
    });
    
    const handleChange = (event) => { 
        setFormData({
          ...formData,
          [event.target.name]: event.target.value,
        });
 
    };

  const formRef = useRef(null);

  const formUrl ="https://script.google.com/macros/s/AKfycbwtZkDdg_TNcWuIwiMKtstj8FKU-P7_3uTXJfZ0-CQHl7ssJ7ScRl0jT-ZfzQbWP0wl/exec"; 
  
 
  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      const response = await fetch(formUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': 'http://localhost:3000',
        },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        console.log('Form submitted successfully');
        // Reset form or display success message
        console.log(response);
        alert(response.msg);
      } else {
        console.error('Form submission failed');
        alert(response.msg);
        // Handle error, e.g., display error message
      }
    } catch (error) {
      console.error('Error submitting form:', error);
      // Handle error, e.g., display error message
    }
  };

  
  return (
    <Container style={{ alignItems: "center", display: "flex", justifyContent: "center" }}>
      <Form className="form-content" ref={formRef} onSubmit={handleSubmit}>
        <Form.Group className="mb-12" controlId="formName"> 
          <Form.Control type="text" className="input-area"  placeholder="Your Name" name="Name" value={formData.name} onChange={handleChange} required />
        </Form.Group>
        <Form.Group className="mb-12 my-4" controlId="formBasicEmail"> 
          <Form.Control type="phone" className="input-area"  placeholder="Phone number" name="Email" value={formData.email} onChange={handleChange}  required />
        </Form.Group>
        <Form.Group className="mb-12"  controlId="Description"> 
          <Form.Control as="textarea" className="input-area" rows={3} name="Message" placeholder="Question, comment or request" value={formData.message} onChange={handleChange}  required />
        </Form.Group>
        <Button  className="form-button" variant="success" type="submit">
          Submit
        </Button>
      </Form>
    </Container>
  );
};
export default ContactForm;